import {
  Component,
  Prop,
  Event,
  EventEmitter,
  h,
  Host,
  Element,
} from '@stencil/core';

/**
 * @slot - Tab content (usually just text, but can include badges)
 */
@Component({
  tag: 'bh-tab-item',
  styleUrl: 'bh-tab-item.css',
  shadow: true,
})
export class BhTabItem {
  @Element() el!: HTMLElement;

  /**
   * Unique identifier for this tab
   */
  @Prop() value!: string;

  /**
   * The label text for the tab
   */
  @Prop() label!: string;

  /**
   * Whether this tab is currently active
   */
  @Prop() active: boolean = false;

  /**
   * Whether this tab is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Emitted when the tab is clicked
   */
  @Event({ bubbles: true, composed: true })
  bhTabItemClick!: EventEmitter<string>;

  private handleClick = (): void => {
    if (!this.disabled) {
      this.bhTabItemClick.emit(this.value);
    }
  };

  render() {
    const buttonClasses = {
      'tab-button': true,
      'tab-button-active': this.active,
      'tab-button-disabled': this.disabled,
    };

    return (
      <Host
        class={{
          'tab-item': true,
          'tab-item-active': this.active,
          'tab-item-disabled': this.disabled,
        }}
      >
        <button
          class={buttonClasses}
          part="button"
          role="tab"
          aria-selected={this.active ? 'true' : 'false'}
          aria-disabled={this.disabled ? 'true' : 'false'}
          disabled={this.disabled}
          onClick={this.handleClick}
          type="button"
        >
          <span class="tab-label" part="label">
            {this.label}
          </span>
          <slot />
        </button>
      </Host>
    );
  }
}
